<template>
  <v-sheet>
    <v-container>
        <base-material-card
            color="green"
            class="autoWidthCard"
        >
            <template v-slot:heading>
                <div class="display-1 font-weight-light">
                    Destinations
                </div>
            </template>
            <div class="text-center">
                <v-icon class="largeIcon primary--text">mdi-share</v-icon>
            </div>
            <h1 class="text-center">Destinations</h1>
            <p class="text-center text--secondary mt-3">Set up Destinations to control where Result Sets from your Batches are sent.</p>

            <div class="listDiv mt-5">
                <h3>Destinations allow you to:</h3>
                <p class="mt-5">
                    <v-icon class="mr-2 primary--text">mdi-amazon</v-icon>Have Batch Result Sets uploaded to an Amazon S3 Bucket when Batches complete</p>
                <p>
                    <v-icon class="mr-2 primary--text">mdi-google</v-icon>Have Batch Result Sets uploaded to an Google Cloud Storage Bucket when Batches complete</p>
                <p>
                    <v-icon class="mr-2 primary--text">mdi-check-circle-outline</v-icon>Set up Destinations once and reuse them across multiple Batches</p>
            </div>

            <div class="w-100 text-center mt-10">
                <v-btn color="blue" @click.stop="drawer = !drawer">
                    <v-icon>mdi-plus</v-icon>
                    Add Destinations
                </v-btn>
            </div>
        </base-material-card>
    </v-container>
    
    <v-navigation-drawer
      v-model="drawer"
      fixed
      temporary
      right
      width="500"
    >
        <v-list-item>
            <v-list-item-content>
                <v-list-item-title class="display-1 blue--text font-weight-bold">Add Destination</v-list-item-title>
            </v-list-item-content>
        </v-list-item>

        <v-divider></v-divider>

        <div class="pa-3">
            <span>What type of Destination would you like to add?</span>
            <v-select
                :items="destinationItems"
                outlined
                label="type of destination"
                dense
                class="mt-2"
            ></v-select>

            <div class="alignCenter">
                <span class="display-1 mr-2 blue--text">Destination Details</span>
                <v-divider></v-divider>
            </div>
            <v-row class="pt-2">
                <v-col md="9">
                    <v-text-field
                        outlined
                        label="Name"
                        dense
                    >
                    </v-text-field>
                </v-col>
                <v-col md="3">
                    <v-switch
                    ></v-switch>
                </v-col>
            </v-row>

            <div class="alignCenter">
                <span class="display-1 mr-2 blue--text">Amazon S3 Settings</span>
                <v-divider></v-divider>
            </div>

            <p class="my-2 text--secondary">We highly recommend creating new IAM credentials just for use by VALUE SERP.</p>
            <span>Access Key ID</span>
            <v-text-field
                dense
                outlined
                label="Access Key ID"
                class="mt-2"
            >
            </v-text-field>
            <span>Secret Access Key</span>
            <v-text-field
                dense
                outlined
                label="Secret Access Key"
                class="mt-2"
            >
            </v-text-field>
            <span>Bucket Name</span>
            <v-text-field
                dense
                outlined
                label="S3 Bucket"
                class="mt-2"
            >
            </v-text-field>
            <span>Path Prefix</span>
            <v-text-field
                dense
                outlined
                label="Path Prefix"
                class="mt-2"
            >
            </v-text-field>
            <p class="text--secondary">Enter an optional path prefix to be prepended to files uploaded to the S3 bucket. The fields %BATCH_ID%, %BATCH_NAME% and %RESULT_SET_ID% will be replaced with the Batch ID, Batch name and Result Set ID respectively.</p>
        </div>

        <div class="w-100 text-right mb-3">
            <v-btn color="warning" small>Cancel</v-btn>
            <v-btn color="blue" small>Save</v-btn>
        </div>
    </v-navigation-drawer>
  </v-sheet>
</template>

<script>

export default {
    components: {
    },

    data:()=> ({
        drawer: null,
        items: [
          { title: 'Home', icon: 'mdi-view-dashboard' },
          { title: 'About', icon: 'mdi-forum' },
        ],
        schedule: null,
        destinationItems: [
            {text: 'Amazon S3 Bucket'},
            {text: 'Google Cloud Storage Bucket'},
        ]
    })
}
</script>

<style>
    .autoWidthCard .v-card--material__heading {
        width: auto !important;
    }

    .largeIcon {
        font-size: 120px !important;
    }

    .listDiv {
        max-width: 700px;
        margin: auto;
    }

    .w-100 {
        width: 100%
    }

    .alignCenter {
        display: flex;
        align-items: center;
    }
</style>